/*
* Handles all data fetching for Twitter API
*/

import p from 'es6-promise';
import $ from 'jquery';

class TwitterData {
  constructor() {
    this.defaultTimeRange = '2018-07-31';
    this.coordinates = [];
    this.topic = '';
  }

  /*
  * Get the first default data range
  */
  getDefaultTwitterData(coordinates) {
    console.log(coordinates)
    let promise = new p.Promise((resolve, reject) => {
      console.log('url ', '/twitter/'+coordinates.lat+'/'+coordinates.lng+'/range/4')
      $.ajax({
        type: 'GET',
        url: '/twitter/'+coordinates.lat+'/'+coordinates.lng+'/range/'+this.defaultTimeRange,
      }).then((res) => {
          console.log('FRDIG: ');
          console.log(res)
      });
    })
  }
} export default TwitterData;



